import React, {Fragment, useContext, useState} from 'react';
import {Swipe, Position} from 'react-swipe-component';
import {PageContext} from '../Contexts/PageContext';
import {MenuContext} from '../Contexts/MenuContext';
import SwipeBtn from './SwipeBtn';






const Swiper = (props) => {
  const [page, setPage] = useContext(PageContext)
  const [isOpen, setIsOpen] = useContext(MenuContext);
  const [xpos, setXpos] = useState(0);
  const [ypos, setYpos] = useState(0);
  const swipeDistance = 250;

    return(
      <Fragment>
      <SwipeBtn left = {props.swipeRight} right={props.swipeLeft} down={"Nav"} up={props.swipeDown || 'Contact'} />
      <Swipe 
      // onSwipe={() => console.log('On Swipe Fired') }
      // onSwipeEnd={() => console.log("Swipe Ended") }
      detectMouse={true}
      detectTouch={true}
      onSwipingLeft={ x => {
        setXpos(x);
          if(x<-1*(swipeDistance)){
          setPage(props.swipeLeft)
        }}}
      onSwipingRight = { x => {
        setXpos(x);
        if(x>swipeDistance){
          setPage(props.swipeRight)
        }
        } }
      onSwipingUp =  { y => {
        setYpos(y);
        if(y< -1*(swipeDistance)){
          setIsOpen(!isOpen);
        } }}
      onSwipingDown =  { y => {
        setYpos(y);
        if(y>swipeDistance){
        setPage(props.swipeDown || 'Contact')
        } }}
      // onSwipeListener = { p => {
      //   if (p.x !== 0) {
      //     console.log(`Swipe x: ${p.x}`)
      //   }
      //   if (p.y !== 0) {
      //     console.log(`Swipe y: ${p.y}`)
      //   }} }
      >
      <div id = 'testdiv' ></div>
      </Swipe>
      </Fragment>
    )
}

export default Swiper;