import React, {Fragment, useState} from 'react';



const NavGuide = props => {




    return(
        <Fragment>
            <div id = 'navGuide-L' className = 'navGuide'>
                {props.left}
            </div>
            <div id = 'navGuide-R' className = 'navGuide'>
                {props.right}
            </div>
            <div id = 'navGuide-U' className = 'navGuide'>
                {props.up}
            </div>
            <div id = 'navGuide-D' className = 'navGuide'>
                {props.down}
            </div>
        </Fragment>
    )
}



export default NavGuide;